import React from 'react';
import styled from 'styled-components';

import './item.css'
import './item1.css'
const Container = styled.div`
height: 100vh;
@media (max-width: 1000px) {
  height: 90vh;
}
  @media (max-width: 769px) {
    height: 80vh;
  }
  @media (max-width: 450px) {
    height: 60vh;
  }
`;
class VideoEmbed extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      liked: [],
      countlike: 0,
      copySuccess: "",
    }

  }

  componentWillMount() {
    console.log(this.props);
    if (this.props && this.props.video) {
      this.setState({ countlike: this.props.video.like });

    }
    console.log(this.state.countlike);
    if (localStorage && localStorage.getItem('liked')) {
      var liked = JSON.parse(localStorage.getItem('liked'));
      this.setState({
        liked: liked,
      });
    }
  }

  onchangeLike = async (event) => {
    // console.log(this.state.liked)
    var xtemp = this.state.liked;

    var k = xtemp.indexOf(this.props.video.id);

    if (k === -1) {

      var like = this.props.video.like + 1;
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id: this.props.video.id, like: like })

      };
      fetch('http://api.huuhao.me:8080/updatelike', requestOptions)
        .then(response => response.json());

      xtemp.push(this.props.video.id);
      this.setState({
        liked: xtemp,
        countlike: like
      });
      localStorage.setItem('liked', JSON.stringify(xtemp));
      console.log(localStorage);
    } else {
      var like = this.props.video.like;
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id: this.props.video.id, like: like })
      };
      fetch('http://api.huuhao.me:8080/updatelike', requestOptions)
        .then(response => response.json());


      let temp1 = xtemp.filter((temp, index) => index !== xtemp.indexOf(this.props.video.id))

      // var temp1 = xtemp.splice(xtemp.indexOf(this.props.video.id), 1);
      console.log(temp1);
      console.log(JSON.stringify(temp1))
      let strTemp = JSON.stringify(temp1);
      localStorage.setItem('liked', strTemp);
      this.setState({
        liked: temp1,
        countlike: like
      });
    }
  }
  onshare = () => {
    navigator.clipboard.writeText('huuhao.me/videoupdate');
    this.setState({
      copySuccess: "Copied"
    })
  }
  saveVideo = () => {

  }
  render() {

    if (this.props.video) {
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ id: this.props.video.id, view: this.props.video.views + 1 })
      };
      fetch('http://api.huuhao.me:8080/updateview', requestOptions)
        .then(response => response.json());
      const { video } = this.props;
      let videoId = video.id;
      let url = `http://api.huuhao.me:8080/video/${videoId}`;
      var res = this.props.video.datevideo.split(" ");
      var like = this.state.countlike;
      var xtemp = this.state.liked;
      var emlementlike = xtemp.indexOf(this.props.video.id) === -1 ? <i className="fa fa-heart active activehoverheart" /> : <i className="fa fa-heart activehoverhearted" />;
      // let k = xtemp.indexOf(this.props.video.id)
      // var iconspan = k === -1 ? <span className='checkedicon checkedicond'><i class="fa fa-thumbs-up" ></i>{" " + video.like}</span> : <span className='checkedicon checkedicond'><i class="fa fa-thumbs-up " ></i>{" " + video.like}</span>
      return (
        <div>
          <Container>
            <div align="center" className="embed-responsive embed-responsive-16by9">
              <video key={url} width={320} height={240} controls poster={`https://img.youtube.com/vi/${video.idvideo}/hqdefault.jpg`} autoPlay>
                <source src={url} type="video/mp4" />

              </video>


            </div>
            <section className="info">
              <div className="metadata">
                <div className="titleAndButtonchung">
                  <span className="title clamp">
                    {video.videosname.replace(".mp4", "")}
                  </span>

                </div>
                <span className="views">{video.views + 1 + " "}{" Lượt views "} {" " + res[0]}</span>
              </div>
              <ul className="actions">
                <li>
                  <button onClick={this.onchangeLike}>
                    {emlementlike}<span>{like === 0 ? 'No one' : '0' + like}</span>
                  </button>
                </li>
                <li>
                  <button><i className="fa fa-thumbs-o-down active" /><span>0</span></button>
                </li>
                <li>
                  <button onClick={this.onshare}><i className="fa fa-share-square-o active" /><span>Share</span>{this.state.copySuccess}</button>
                </li>
                <li>
                  <button onClick={this.saveVideo}  ><i href={`http://api.huuhao.me:8080/video/${videoId}`} download className="fa fa-floppy-o active" /><span>Save</span></button>
                </li>



                <li>
                  <button>
                    <i className="fa fa-bug active" aria-hidden="true" /><span>Report</span>
                  </button>
                </li>
              </ul>
              <div className="channel">
                <div className="metadata">
                  <div className="info">
                  </div>
                </div>
              </div>
            </section>

          </Container>
        </div>
      );
    }
    return "";
  }
}
export default VideoEmbed;
