import React, { Component } from 'react';
import NavFirst from './bannerUI/navFirst';
import NavSecond from './bannerUI/navSecond';
import NavThird from './bannerUI/navThird';
class Banner extends Component {
    render() {
        return (
            <div>
                <section className="tm-banner">
                    {/* Flexslider */}
                    <div className="flexslider flexslider-banner">
                        <ul className="slides">
                            <li>
                                <div className="tm-banner-inner">
                                    <h1 className="tm-banner-title">Safe<span className="tm-yellow-text">  for  </span>everyone</h1>
                                    <p className="tm-banner-subtitle"></p>
                                    <a href="#more" className="tm-banner-link">Learn More</a>
                                </div>
                                <img src="img/banner-1.jpg" alt="Image" />
                            </li>
                            <li>
                                <div className="tm-banner-inner">
                                    <h1 className="tm-banner-title">Protect <span className="tm-yellow-text">Yourself</span></h1>
                                    <p className="tm-banner-subtitle"></p>
                                    <a href="#more" className="tm-banner-link">Learn More</a>
                                </div>
                                <img src="img/banner-2.jpg" alt="Image" />
                            </li>
                            <li>
                                <div className="tm-banner-inner">
                                    <h1 className="tm-banner-title">News<span className="tm-yellow-text"> about </span> Covid 19</h1>
                                    <p className="tm-banner-subtitle">WHO</p>
                                    <a href="#more" className="tm-banner-link">Read More</a>
                                </div>
                                <img src="img/banner-3.jpg" alt="Image" />
                            </li>
                        </ul>
                    </div>
                </section>
                {/* gray bg */}
                <section className="container tm-home-section-1" id="more">

                    <NavFirst></NavFirst>
                    <NavSecond></NavSecond>
                    <NavThird></NavThird>

                </section>
                
            </div>
        );
    }
}

export default Banner;
