import React, { Component } from 'react';

class NavFirst extends Component {
    render() {
        return (
            <div> 
                   <div className="row">
                     <div className="col-lg-4 col-md-4 col-sm-6">
                  <div className="tm-home-box-1 tm-home-box-1-2 tm-home-box-1-center">
                    <img src="img/index-01.jpg" alt="image" className="img-responsive" />
                    <a href="https://moh.gov.vn/">
                      <div className="tm-green-gradient-bg tm-city-price-container">
                        <span>Bộ Y tế</span>
                      </div>	
                    </a>			
                  </div>				
                </div>
                <div className="col-lg-4 col-md-4 col-sm-6">
                  <div className="tm-home-box-1 tm-home-box-1-2 tm-home-box-1-center">
                    <img src="img/index-02.jpg" alt="image" className="img-responsive" />
                    <a href="https://www.who.int/">
                      <div className="tm-yellow-gradient-bg tm-city-price-container">
                        <span>WHO  (World Health Organization)</span>
                  
                      </div>	
                    </a>			
                  </div>				
                </div>
                <div className="col-lg-4 col-md-4 col-sm-6">
                  <div className="tm-home-box-1 tm-home-box-1-2 tm-home-box-1-right">
                    <img src="img/index-03.jpg" alt="image" className="img-responsive" />
                    <a href="https://vietnam.travel/things-to-do/information-travellers-novel-coronavirus-vietnam">
                      <div className="tm-red-gradient-bg tm-city-price-container">
                        <span>Việt Nam</span>
                      </div>	
                    </a>					
                  </div>				
                </div>
              </div>
          </div>
        );
    }
}

export default NavFirst;
