import React, { Component } from 'react';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Videoitem from './videoitem';
import Header from "../Header/Header"
import Loading from '../news/loading';
class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            contentvideos: [],
            checkdata: false

        }
        window.addEventListener('scroll', this.checkScroll, false);
        window.addEventListener('resize', this.checkScroll, false);
    }
    componentWillMount() {

        fetch('http://api.huuhao.me:8080/getallvideoalltoday')
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                if(myJson!=null)
                {
                  this.setState({
                    checkdata:true
                  })
                }
                this.setState({
                    contentvideos: myJson,

                })
            });
    }
    checkScroll() {
        var videos = document.getElementsByTagName("video");
        var fraction = 0.5; // Play when 80% of the player is visible.

        for (var i = 0; i < videos.length; i++) {

            var video = videos[i];

            var x = video.offsetLeft, y = video.offsetTop, w = video.offsetWidth, h = video.offsetHeight, r = x + w, //right
                b = y + h, //bottom
                visibleX, visibleY, visible;

            visibleX = Math.max(0, Math.min(w, window.pageXOffset + window.innerWidth - x, r - window.pageXOffset));
            visibleY = Math.max(0, Math.min(h, window.pageYOffset + window.innerHeight - y, b - window.pageYOffset));

            visible = visibleX * visibleY / (w * h);

            if (visible > fraction) {
                video.play();
            } else {
                video.pause();
            }

        }

    }

    render() {
        var checkdata = this.state.checkdata === true ? "" : <Loading></Loading>;
        var element1 = this.state.contentvideos.map((item, index) => {

            return <Videoitem index={index} item={item} onseclectvideo={this.onseclectvideo}></Videoitem>
        });
        return (


            <div>
                <div className='editmarginbottom'>
                    <Header></Header>
                </div>
                {checkdata}
                {element1}
            </div>
        )
    }
}

export default Home;
