import React from 'react';

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import './header.css'

import Box from '@material-ui/core/Box';
import VideoLibraryIcon from '@material-ui/icons/VideoLibrary';
import FiberNewIcon from '@material-ui/icons/FiberNew';
import HomeIcon from '@material-ui/icons/Home';
import { Link } from 'react-router-dom'
import { layoutGenerator } from 'react-break';
import MovieIcon from '@material-ui/icons/Movie';
const layout = layoutGenerator({

	phablet: 0,
	desktop: 992,
});
const OnAtMostPhablet = layout.isAtMost('phablet');
const OnDesktop = layout.is('desktop');
const useStyles = makeStyles((theme) => ({
	root: {
		flexGrow: 1,
		width: '100%',
		backgroundColor: theme.palette.background.paper,
	},
}));

export default function ScrollableTabsButtonForce() {
	const classes = useStyles();
	const [value, setValue] = React.useState(0);

	const handleChange = (event, newValue) => {
		setValue(newValue);
	};

	return (
		<div className={classes.root}>
		
				<AppBar position="static" color="default">
					<Tabs
						value={value}
						onChange={handleChange}
						variant="scrollable"
						scrollButtons="on"
						indicatorColor="primary"
						textColor="primary"
						aria-label="scrollable force tabs example"
					>
						<Tab label="Home" icon={<HomeIcon />} to="/" component={Link} />
						<Tab label="Videos" icon={<VideoLibraryIcon />} to="/videoupdate" component={Link} />
						<Tab label="News" icon={<FiberNewIcon />} to="/news" component={Link} />
						<Tab label="Watch" icon={<MovieIcon />} to="/videos" component={Link} />
					</Tabs>
				</AppBar>

		</div>
	);
}
