import React, { Component } from 'react';
import EcoIcon from '@material-ui/icons/Eco';
import AccessibilityIcon from '@material-ui/icons/Accessibility';
import './item.css'
import Videoitem from './videoitem';
import VideoEmbed from './video'
import TodayIcon from '@material-ui/icons/Today';
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';

import Header from '../Header/Header'
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Loading from '../news/loading';
import MovieIcon from '@material-ui/icons/Movie';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

import VideoLibraryIcon from '@material-ui/icons/VideoLibrary';
import FiberNewIcon from '@material-ui/icons/FiberNew';
import HomeIcon from '@material-ui/icons/Home';
import { Link } from 'react-router-dom'
import { layoutGenerator } from 'react-break';
const layout = layoutGenerator({

    phablet: 0,
    desktop: 992,
});
const OnAtMostPhablet = layout.isAtMost('phablet');
const OnDesktop = layout.is('desktop');
class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            contentvideos: [],
            videoselect: [],
            checkdata: false
        }
    }
    componentWillMount() {

        fetch('http://api.huuhao.me:8080/getallvideoalltoday')
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                if(myJson!=null)
                {
                  this.setState({
                    checkdata:true
                  })
                }
                this.setState({
                    contentvideos: myJson,
                    videoselect: myJson[0]
                })
            });
    }
    onseclectvideo = (id) => {

        var index = this.findIndex(id);
        console.log(index);
        this.setState({
            videoselect: this.state.contentvideos[index]
        })
    }
    findIndex = (id) => {
        var items = this.state.contentvideos;
        var re = -1;
        items.forEach((item, index) => {
            if (item.id === id) {
                re = index;
            }
        });
        return re;
    }
    onGetNewsvirus = () => {
        console.log("dcdcd");
        fetch('http://api.huuhao.me:8080/getallvideoalltoday')
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                this.setState({
                    contentvideos: myJson,
                    videoselect: myJson[0]
                })
            });
        console.log(this.state);
    }
    onGetNewstuoitre = () => {
        fetch('http://api.huuhao.me:8080/getvideotuoitre')
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                this.setState({
                    contentvideos: myJson,
                    videoselect: myJson[0]
                })
            });


    }
    onGetNewsthanhnien = () => {
        fetch('http://api.huuhao.me:8080/getvideothanhnien')
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                this.setState({
                    contentvideos: myJson,
                    videoselect: myJson[0]
                })
            });
    }
    onGetNewstodaylastest = () => {
        fetch('http://api.huuhao.me:8080//getnewlastest')
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                this.setState({
                    contentvideos: myJson,
                    videoselect: myJson[0]
                })
            });
    }
    render() {
        var checkdata = this.state.checkdata === true ? "" : <Loading></Loading>;
        var element1 = this.state.contentvideos.map((item, index) => {

            return <Videoitem index={index} item={item} onseclectvideo={this.onseclectvideo}></Videoitem>
        });
        var videoselect = this.state.videoselect.length === 0 ? <VideoEmbed video={this.state.contentvideos[0]} /> : <VideoEmbed video={this.state.videoselect} />;
        return (


            <div>
                <OnDesktop>

                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <Header></Header>
                        </Grid>
                        <Grid item xs={3} sm={2}>
                            <Divider />
                            <List className="fixfont">

                                <ListItem button onClick={this.onGetNewsvirus} >
                                    <ListItemIcon> <EcoIcon /> </ListItemIcon>
                                    <ListItemText primary='Covid' />
                                </ListItem>
                                <ListItem button onClick={this.onGetNewstuoitre} >
                                    <ListItemIcon> <AccessibilityIcon /> </ListItemIcon>
                                    <ListItemText primary='Tuổi trẻ' />
                                </ListItem>
                                <ListItem button onClick={this.onGetNewsthanhnien}>
                                    <ListItemIcon> <ArrowRightAltIcon /> </ListItemIcon>
                                    <ListItemText primary='Thanh niên' />
                                </ListItem>
                                <ListItem button onClick={this.onGetNewstodaylastest} >
                                    <ListItemIcon> <TodayIcon /> </ListItemIcon>
                                    <ListItemText primary='Tin mới' />
                                </ListItem>
                            </List>
                            <Divider />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <Paper>
                                {videoselect}
                            </Paper>
                        </Grid>
                        <Grid item xs={6} sm={4} className='exam11'>
                            {checkdata}
                            {element1}
                        </Grid>
                    </Grid>
                </OnDesktop>

                <OnAtMostPhablet>
                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <AppBar position="static" color="default">
                                <Tabs
                                    
                                   
                                    variant="scrollable"
                                    scrollButtons="on"
                                    indicatorColor="primary"
                                    textColor="primary"
                                    aria-label="scrollable force tabs example"
                                >
                                    <Tab label="Home" icon={<HomeIcon />} to="/" component={Link} />
                                    <Tab label="Corona" icon={<VideoLibraryIcon />} to="/videoupdate" component={Link} />
                                    <Tab label="Thanh niên" icon={<VideoLibraryIcon />} onClick={this.onGetNewsthanhnien} />
                                    <Tab label="Tuổi trẻ" icon={<VideoLibraryIcon />} onClick={this.onGetNewstuoitre} />
                                    <Tab label="Tin mới" icon={<VideoLibraryIcon />} onClick={this.onGetNewstodaylastest} />
                                    <Tab label="News" icon={<FiberNewIcon />} to="/news" component={Link} />
                                    <Tab label="Watch" icon={<MovieIcon />} to="/videos" component={Link} />
                                </Tabs>
                            </AppBar>
                        </Grid>
            
                        <Grid item xs={12} sm={12}>
                            {videoselect}
                    

                        </Grid>

                        <Grid item xs={12} sm={12} className='exam11'>
                            {element1}
                        </Grid>

                    </Grid>

                </OnAtMostPhablet>

            </div>
        )
    }
}

export default Home;
