import React, { Component } from 'react';
import './style.css'
class Home extends Component {
    constructor(props) {
        super(props)
        this.state = {
            contentvideos: [],
            videoselect: [],
            checkdata: false
        }
    }
    componentWillMount() {

        fetch('http://huuhao.me:8080/getinfodowntoday')
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                if (myJson != null) {
                    this.setState({
                        checkdata: true
                    })
                }
                this.setState({
                    contentvideos: myJson,
                    videoselect: myJson[0]
                })
            });
    }
    counttodaydown() {

        let datenow = getdatenow(0);
        console.log(datenow);
        let count = 0;
        this.state.contentvideos.map((item, index) => {
            let temp = item.datevideo.split(" ");
            if (datenow.localeCompare(temp[0]) === 0) {
                count++;
            }

        });
        return count;
    }
    counttodaydownqua() {

        let datenow = getdatenow(1);
        console.log(datenow);
        let count = 0;
        this.state.contentvideos.map((item, index) => {
            let temp = item.datevideo.split(" ");
            if (datenow.localeCompare(temp[0]) === 0) {
                count++;
            }

        });
        return count;

    }
    counttodaydownkia() {

        let datenow = getdatenow(2);
        console.log(datenow);
        let count = 0;
        this.state.contentvideos.map((item, index) => {
            let temp = item.datevideo.split(" ");
            if (datenow.localeCompare(temp[0]) === 0) {
                count++;
            }
        });
        return count;

    }
    render() {
        var x = this.counttodaydown();
        var x1 = this.counttodaydownqua();
        var x2 = this.counttodaydownkia();
        var element1 = this.state.contentvideos.map((item, index) => {

            return <tr>
                <td>{item.id}</td>
                <td>{item.idvideo}</td>
                <td>{item.videosname}</td>
                <td>{item.datevideo}</td>
                <td>{item.like}</td>
                <td>{item.views}</td>
                <td>{item.idchannel}</td>
            </tr>;
        });

        return (


            <div >
                <h3 className="editcenter1">Kết quả tải được 3 ngày gần nhất</h3>
                <h5 className="editcenter1">Hôm nay tải hôm nay {datatimenow()}: {x}</h5>
                <h5 className="editcenter1">Số video ngày {getdateStrinh(1) + " tải được"}: {x1}</h5>
                <h5 className="editcenter1">Số video ngày {getdateStrinh(2)} tải được: {x2}</h5>
                <table className="editcenter" >

                    <td>ID</td>
                    <td>VideoID</td>
                    <td>Video Name</td>
                    <td>Date downloaded</td>
                    <td>Like</td>
                    <td>View</td>
                    <td>Idchannel</td>
                    {element1}




                </table>
            </div>
        )
    }
}
function getdatenow(sub) {

    var today = new Date();
    var date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + (Number(today.getDate()) - Number(sub)).toString();
    //console.log(datetemp);
    return date;
}
function getdateStrinh(nub) {
    var today = new Date();
    var datetemp;
    datetemp = Number(today.getDate() - Number(nub)) + "/" + (today.getMonth() + 1) + "/" + today.getFullYear();
    return datetemp;
}
function datatimenow() {
    var today = new Date();
    var date =  today.getDate()+ '-' + (today.getMonth() + 1) + '-' +today.getFullYear() ;
    var time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
    var dateTime =time+" "+date;
    return dateTime;
}
export default Home;
