import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import './App.css';
import Home from "./components/Home";
import News from "./components/News";
import videoupdate from "./components/videoUpdate"
import Videofb  from "./components/watchfacebookapp/watchFB"
import Admin from "./components/admin"
class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div className="App">


          <Switch>
          <Route exact path="/" component={Home}></Route>
          <Route exact path="/News" component={News}></Route>
          <Route exact path="/videos" component={Videofb}></Route>
          <Route exact path="/videoupdate" component={videoupdate}></Route>   
          <Route exact path="/adminhuuhaome" component={Admin}></Route> 
          </Switch>
        </div>
      </BrowserRouter>
    )
  }
}

export default App;
