import React from 'react';
import Loading from './loading';
class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      news: [],
      videos: [],
      checkdata: false
    }
  }
  componentDidMount() {
    fetch('https://cryptic-ravine-96718.herokuapp.com/')
      .then((response) => {
        return response.json();
      })
      .then((myJson) => {
        if(myJson.news!=null)
        {
          this.setState({
            checkdata:true
          })
        }
        this.setState({
          news: myJson.news
        })
      });

  }

  render() {

    var checkdata = this.state.checkdata === true ? "" : <Loading></Loading>;
    return (
      <div>

        <section className="tm-white-bg section-padding-bottom">
          <div className="container">
            <div className="row">
              <div className="tm-section-header section-margin-top">
                <div className="col-lg-4 col-md-3 col-sm-3"><hr /></div>
                <div className="col-lg-4 col-md-6 col-sm-6"><h2 className="tm-section-title">News</h2></div>
                  {checkdata}
                <div className="col-lg-4 col-md-3 col-sm-3"><hr /></div>
              </div>

            </div>
            <div className="row">
              {this.state.news.map((item, index) => {
                return (

                  <div className="col-lg-3 col-md-3 col-sm-6 col-xs-6 col-xxs-12" key={index} >
                    <div className="tm-tours-box-2 exam1" >
                      <img src={item.img} alt="image" height='150px' width='250px' />
                      <div className="tm-tours-box-2-info">
                        <h3 className="margin-bottom-15">{item.title}</h3>


                      </div>


                      <a href={item.link} className="tm-tours-box-2-link editviewtag1 ">Read more</a>
                    </div>
                  </div>
                )
              })}

            </div>
            <div className="row">
              <div className="col-lg-12">
                <p className="home-description">
                </p>
              </div>
            </div>
          </div>
        </section>
      </div>

    )
  }
}

export default App;

