import React, { Component } from 'react';
import Youtube from '../../youtubecorona/Router.youtube'
class NavSecond extends Component {
    render() {
        return (
            <div> 
                <div className="section-margin-top">
                        <div className="row">
                            <div className="tm-section-header">
                                <div className="col-lg-3 col-md-3 col-sm-3"><hr /></div>
                                <div className="col-lg-6 col-md-6 col-sm-6"><h2 className="tm-section-title">Corona videos</h2></div>
                                <div className="col-lg-3 col-md-3 col-sm-3"><hr /></div>
                            </div>
                        </div>
                        <div className="row">
                            <Youtube></Youtube>
                        </div>
                        <div className="row">
                            <div className="col-lg-12">
                                <p className="home-description">Giữ ấm cơ thể, vệ sinh cá nhân, rửa tay thường xuyên bằng xà phòng, súc họng bằng nước sát khuẩn miệng trước khi đi ngủ, sau khi ngủ dậy cũng như trước và sau khi tiếp xúc với cộng đồng cư dân đông đúc để dự phòng nhiễm bệnh. Anh chị nên mua lọ sát khuẩn tay nhanh mang theo bên mình nhé</p>
                            </div>
                        </div>
                    </div>
           
          </div>
        );
    }
}

export default NavSecond;
