import React, { Component } from 'react';
import './item.css'

import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Divider from '@material-ui/core/Divider';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';

import './item.css'
class Home extends Component {

  onseclectvideo = () => {
    this.props.onseclectvideo(this.props.item.id);
  }

  render() {
    var { item, index} = this.props;
    var res = this.props.item.datevideo.split(" ");

    return (
      <div key={index} >
        <List>
          <ListItem alignItems="flex-start" onClick={this.onseclectvideo} className="addhovervideolisr">
            <ListItemAvatar className="opacityforimg">
              <img  alt="Remy Sharp" src={'https://img.youtube.com/vi/' + item.idvideo + '/hqdefault.jpg'} />
            </ListItemAvatar>
            <div className='titleitem'>
            <ListItemText
              primary={item.videosname.replace(".mp4", "")}
              secondary={
                <React.Fragment>
                  <div className='editicon'>
                    {item.like +' Like             -'}
                    {' '+item.views +' Views'} 
                    {' '+res[0]} 
                  </div>
                </React.Fragment>
              } 
            /></div>
          </ListItem>
          <Divider variant="inset" component="li" />
        </List>
      </div>
    )
  }
}

export default Home;
